import React from 'react'
import ReactDOM from 'react-dom'


function Header() {
    return (
        <nav className="indigo">
            <div className="nav-wrapper">
                <a href="#" className="brand-logo">React Movies</a>
                <ul id="nav-mobile" className="right hide-on-med-and-down">
                    <li><a href="!#">Sass</a></li>
                </ul>
            </div>
        </nav>
    )
}

export {Header}


